body
{
  margin: 0;
	padding: 0;
  background-color: #ffffff;
  overflow-x: hidden;
}

.container
{
  width: 100%;
  height: 100%;
}

.text-box
{
  padding-top: 50px;
  padding-bottom: 100px;
}

.title
{
  color: #effd56;
  font-size: 2.5em;
  position: absolute;
  top: 32px;
  left: 20px;
  margin: 0;
}

.headline
{
  font-size: 3em;
  text-align: center;
}

.sub-title
{
  font-size: 1.8em;
  height: auto;
  width: 60%;
  margin-left: auto;
  margin-right: auto;
}

.paragraph-container
{
  height: auto;
  width: 60%;
  margin-left: auto;
  margin-right: auto;
  line-height: 1.5em;
}

.top-page
{
  display: block;
  height: 100%;  
  width: 100%; 
  position: relative;
  background-color: #effd56;
  top: 0;
  left: -10px;
  padding: 60px;
  z-index: -1;
  transform:rotate(-1deg);
}

h1,
h2,
h3,
p
{
	text-transform: uppercase;
  font-family: 'Quicksand', sans-serif;
}

p{color: #000000;}

.strip
{
    position: absolute;
    height: 75%;
    width: 100%;
    top: 0;
    left: 0;
    background-color: #51a866;
    padding: 10px;
    transform:rotate(1deg);
}
  
/* ================================================================================================ */

  .close {
    position: absolute;
    right: 32px;
    top: 32px;
    width: 32px;
    height: 32px;
    opacity: 0.5;
  }
  .close:hover {
    opacity: 1;
  }
  .close:before, .close:after
  {
    position: absolute;
    left: 15px;
    content: ' ';
    height: 33px;
    width: 2px;
    background-color: #effd56;
  }
  .close:before {
    transform: rotate(45deg);
  }
  .close:after {
    transform: rotate(-45deg);
  } 

/* ================================================================================================ */

/* 3D Image Slider */

.img-container 
{
  width: 83%;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
  
.img-size
{
  height: auto;
  width: 100%;
  margin: auto;
}

/* Next & previous buttons */
.prev, .next {
  cursor: pointer;
  position: absolute;
  width: auto;
  padding: 16px;
  color: #000000;
  font-weight: bold;
  font-size: 28px;
  transition: 0.6s ease;
  align-items: center;
}

/* Position the "next button" to the right */
.next {
  right: 100px;
  border-radius: 50%;
}

.prev {
  left: 100px;
  border-radius: 50%;
}

/* On hover, add a black background color with a little bit see-through */
.prev:hover, .next:hover {
  background-color: rgba(0,0,0,0.8);
  color: #2fff05;
}

.button--inactive 
{
  opacity: 0.2;
}

.cards-wrapper 
{
  display: flex;
  flex-direction: column;
  align-items: center;
}

.cards-container {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}
  
.box {
  width: 100%;
  height: auto;
  box-shadow: 0px 0px 2rem 0px #888888;
  background-color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
}

.box:nth-child(2n) {
  transform: scale(0.85);
  z-index: -1;
}

.box:nth-child(2) {
left: 8%;
}

.box:nth-child(2n + 1) {
  transform: scale(0.75);
  z-index: -2;
}

.box:nth-child(1) {
left: 15%;
}

.hide-box {
  display: none;
}

@media screen and (max-width: 800px)
{
  .next 
  {
    right: 0;
  }
  
  .prev 
  {
    left: 0;
  }

  .prev:hover, .next:hover 
  {
    background-color: white;
    color: black;
  }

  .cards-container 
  {
    height: 20rem;
  }
}

.move-out-from-left {
  animation: moveOutLeft 0.5s ease-in-out;
}

.move-out-from-right {
  animation: moveOutRight 0.5s ease-in-out;
}

.move-to-position5-from-left {
  animation: moveToP5Left 0.5s ease-in-out;
}

.move-to-position4-from-left {
  animation: moveToP4Left 0.5s ease-in-out;
}

.move-to-position3-from-left {
  animation: moveToP3Left 0.5s ease-in-out;
}

.move-to-position2-from-left {
  animation: moveToP2Left 0.5s ease-in-out;
}


.move-to-position1-from-left{
  animation: moveToP1Left 0.5s ease-in-out;
}

.move-to-position5-from-right{
  animation: moveToP5Right 0.5s ease-in-out;
}

.move-to-position4-from-right{
  animation: moveToP4Right 0.5s ease-in-out;
}

.move-to-position3-from-right{
  animation: moveToP3Right 0.5s ease-in-out;
}

.move-to-position2-from-right{
  animation: moveToP2Right 0.5s ease-in-out;
}

.move-to-position1-from-right{
  animation: moveToP1Right 0.5s ease-in-out;
}
  
@keyframes moveOutLeft {
  0% {
      transform: scale(0.75) translateX(0%);
      opacity: 1;
  }
  50% {
      transform: scale(0.5) translateX(-150%);
      opacity: 0.5;
  }
  100% {
      transform: scale(0.25) translateX(0%);
      opacity: 0;
  }
}


@keyframes moveOutRight {
  0% {
      transform: scale(0.75) translateX(0%);
      opacity: 1;
  }
  50% {
      transform: scale(0.5) translateX(150%);
      opacity: 0.5;
  }
  100% {
      transform: scale(0.25) translateX(0%);
      opacity: 0;
  }
}